const getters = {
  flagData: state => {
    return state.titleData.filter(item => item.flag)
  }
}

const actions = {
  initdata (context, data) {
    context.commit('INITDATA', data)
  },
  wxapi (context, data) {
    context.commit('WXAPI', data)
  }
}

const mutations = {
  [INITDATA] (state, data) {
    state.titleData[data].flag = true
  },
  [WXAPI] (state, payload) {
    state.wxapi.timestamp = payload.timestamp
    state.wxapi.nonceStr = payload.nonceStr
    state.wxapi.signature = payload.signature
  },
  [COMEIN] (state) {
    state.iscomeFirst = false
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



